import React, {useEffect} from 'react';
import {Link} from 'react-router-dom';

const Work = (props) => {

    useEffect(()=>{
        const workBtnContainer = document.querySelector('.work__categories');
        const projectContainer = document.querySelector('.work__projects');
        const projects = document.querySelectorAll('.project');
        workBtnContainer.addEventListener('click', (e) => {
            const filter = e.target.dataset.filter || e.target.parentNode.dataset.filter;
            if(filter == null){
                return;
            }
            
            const active = document.querySelector('.category__btn.selected');
            active.classList.remove('selected');
            const target = e.target.nodeName === 'BUTTON' ? e.target : e.target.parentNode;
            target.classList.add('selected');
        
            projectContainer.classList.add('anim-out');
            
        
            setTimeout(()=>{
                projects.forEach((project) => {
                
                    if(filter === '*' || filter === project.dataset.type){
                        project.classList.remove('invisible');
                    }else{
                        project.classList.add('invisible');
                    }
                });
                projectContainer.classList.remove('anim-out');
            }, 300);
        
        });
        });

        return(
        <section id="work" className="section">
        <h1>My work</h1>
        <h3>Projects</h3>
        <div className="work__categories">
            <button className="category__btn selected" data-filter="*">All<span className="category__count">7</span></button>
            <button className="category__btn" data-filter="front-end">Front-end<span className="category__count">5</span></button>
            <button className="category__btn" data-filter="back-end">Back-end<span className="category__count">2</span></button>
        </div>
        <div className="work__projects">
               <Link to="/detail/1" className="project" data-type="front-end">
                <img className="project__img" src="imgs/bussiness.png" alt=""/>
                <div className="project__description">
                    <h3>Bussiness CardMaker</h3>
                    <span>명함 제작 사이트</span>
                </div>
                </Link>
                <Link to="/detail/2" className="project" data-type="back-end">
                <img className="project__img" src="imgs/nongzip.png" alt=""/>
                <div className="project__description">
                    <h3>Nongzip</h3>
                    <span>농집 유지보수</span>
                </div>
                </Link>
                <Link to="/detail/3" className="project" data-type="back-end">
                <img className="project__img" src="imgs/edu.png" alt=""/>
                <div className="project__description">
                    <h3>Edu Academy</h3>
                    <span>서민금융진흥원 금융교육포털</span>
                </div>
                </Link>
                <Link to="/detail/4" className="project" data-type="front-end">
                <img className="project__img" src="imgs/youtube.png" alt=""/>
                <div className="project__description">
                    <h3>Youtube Clone</h3>
                    <span>Youtbue 클론코딩</span>
                </div>
                </Link>
                <Link to="/detail/5" className="project" data-type="front-end">
                <img className="project__img" src="imgs/incheon.png" alt=""/>
                <div className="project__description">
                    <h3>Portfolio</h3>
                    <span>포트폴리오 사이트</span>
                </div>
                </Link>
                <Link to="/detail/6" className="project" data-type="front-end">
                <img className="project__img" src="imgs/habit_tracker.png" alt=""/>
                <div className="project__description">
                    <h3>Habit Tracker</h3>
                    <span>간단한 취미 관리 사이트</span>
                </div>
                </Link>
                <Link to="/detail/7" className="project" data-type="front-end">
                <img className="project__img" src="imgs/shopping-game.png" alt=""/>
                <div className="project__description">
                    <h3>Shopping Game</h3>
                    <span>간단한 쇼핑게임 사이트</span>
                </div>
                </Link>
        </div>
    </section> 
            )
            };

export default Work;