import React, { Component } from 'react';
import styles from './about.module.css';
class About extends Component {
    render() {
        return (
        <section id="about" className="section">
        <h1>About me</h1>
        <p>
            안녕하세요 이제 막 2년차 개발자가 된 장인천입니다. 
            SI 기업인 상상스토리에서 1년 3개월 근무를 하였으며 주로 전자정부 프레임워크를 이용하여
            다수의 공공기업, 정부 프로젝트 참여와 유지보수 업무를 진행하였습니다. 현재는 보다 다양한 개발자로서의 전문성을 갖기 위해 ReacJS, Node 관련 공부를 하고 있으며 현재는 더욱 더 깊이있게 공부하기 위하여 티스토리 블로그<a class={styles.tistory} href="https://ing-cheon.tistory.com/" target="blank">(https://ing-cheon.tistory.com/)</a>에 포스팅을 하고 있습니다.
        </p>
        <div className="about__majors">
            <div className="major">
                <div className="major__icon">
                    <i className="fab fa-react"></i>
                </div>
                <div className="major__title">React</div>
                <div className="major__description">
                    리엑트 프레임워크
                </div>
            </div>
            <div className="major">
                <div className="major__icon">
                    <i className="fab fa-html5"></i>
                </div>
                <div className="major__title">Html</div>
                <div className="major__description">
                    Html 관련
                </div>
            </div>
            <div className="major">
                <div className="major__icon">
                    <i className="fab fa-java"></i>
                </div>
                <div className="major__title">Java</div>
                <div className="major__description">
                    전자정부 프레임워크
                </div>
            </div>            
        </div>
        <div className="about__jobs">
            <div className="job">
                <img src="imgs/jobs/sangsang.png" alt="sangs" className="job__log"/>
            </div>
            <div className="job__description">
                <p className="job__name">데이터사업팀 개발팀 사원</p>
                <p className="job__period">2019.08 ~ 2020.11</p>
            </div>            
        </div>
    </section>
        );
    }
}

export default About;