import React, {useEffect} from 'react';

const Header = (props) => {
    useEffect(()=>{
        // navbar 를  투명, 불투명 처리하는 부분
const navbar = document.querySelector('#navbar');
const navbarHeight = navbar.getBoundingClientRect().height;
document.addEventListener('scroll', () => {
    //console.log(window.scrollY);
    //console.log('navbarHeight',navbarHeight); 길이 확인용
    if(window.scrollY > navbarHeight){
        navbar.classList.add('navbar--dark');
    }else{
        navbar.classList.remove('navbar--dark');
    }
})


// Handle scrolling 

    const navbarMenu = document.querySelector('.navbar__menu');
    
    navbarMenu.addEventListener('click', (event)=>{            
    navbarMenu.classList.remove('open');
    
})

//반응형 Navbar 만들기
const navbarToggleBtn = document.querySelector('.navbar__toggle-btn');
navbarToggleBtn.addEventListener('click', () => {
    navbarMenu.classList.toggle('open');
})

function scrollIntoView(selector) {
    const scollTo = document.querySelector(selector);
    scollTo.scrollIntoView({behavior: 'smooth'}); 
}
    })



    return(
        <nav id="navbar">
        <div className="navbar__log">
            <i className="fab fa-diaspora"></i>
            <a href="#">Incheon</a>
        </div>        
            <ul className="navbar__menu">
                <li className="navbar__menu__item" onClick={() => {
        document.getElementById('home').scrollIntoView({behavior: 'smooth'});}}>Home</li>
                <li className="navbar__menu__item" onClick={() => {
        document.getElementById('about').scrollIntoView({behavior: 'smooth'});}}>About</li>
                <li className="navbar__menu__item" onClick={() => {
        document.getElementById('skills').scrollIntoView({behavior: 'smooth'});}}>Skills</li>
                <li className="navbar__menu__item" onClick={() => {
        document.getElementById('work').scrollIntoView({behavior: 'smooth'});}}>My work</li>                
                <li className="navbar__menu__item" onClick={() => {
        document.getElementById('contact').scrollIntoView({behavior: 'smooth'});}}>Contact</li>
            </ul>              
        <button className="navbar__toggle-btn">
            <i className="fas fa-bars"></i>
        </button>
      </nav>       
    );
};

export default Header;