import React from 'react';
import styles from './contact.module.css';

const Contact = (props) => {
       return(
        <section id="contact" className={styles.section}>
        <h1 className={styles.contact__title}>Let's talk</h1>
        <h2 className={styles.contact__email}>icjang123@naver.com</h2>
        <div className={styles.contact__links}>
            <a href="https://github.com/JangHarry" target="blank">
                <i className="fab fa-github"></i>
            </a>
            <a href="https://www.instagram.com/ing_cheon/?hl=ko" target="blank">
                <i className="fab fa-instagram-square"></i>
            </a>
            <a href="https://open.kakao.com/o/soGATmEc" target="blank">
                <i className="fas fa-comment-dots"></i>
            </a>
        </div>
        <div className={styles.contact__rights}>
            <ul>
                <li>
                    <p className={styles.tit}>Where i live</p>
                    <p className={styles.body}>Gimpo, Gyunggi-do, Republic of Korea</p>
                </li>
                <li>
                    <p className={styles.tit}>Give me a call</p>
                    <p className={styles.body}>T. +82 (0)10 9730 0143</p>
                </li>                
            </ul>
        </div>
    </section>
       )
    };

export default Contact;