import React from 'react';
import styles from './skillset.module.css';


const Skillset = ({item}) => {         
       return(          
        <div className="skill">
        <div className="skill__description">
            <span>{item.id}</span>
            <span>{item.per}</span>
        </div>
        <div className="skill__bar">
            <div className={styles.skill__value} style={{width : item.per}}></div>
        </div>
        </div>
       )     
    };

export default Skillset;