import React from 'react';
import styles from './Home.module.css';

const Home = (props) => {
    return(
        <section id="home">
        <div className="home__container">
            <img 
            src="imgs/incheon.png" 
            alt="Incheon's profile photo" 
            className="home__avatar"/>
            <h1 className="home__title">Hello, I am Incheon</h1>
            <h2 className="home__description">안녕하세요 인천입니다</h2>
            <button className="home__contact" onClick={() => {
        document.getElementById('contact').scrollIntoView({behavior: 'smooth'});}}>Contact me</button>
        </div>
    </section>
    )
};

export default Home;